:root {
  --primary: hsl(47, 88%, 63%);
  --neutral-100: hsl(0, 0%, 100%);
  --neutral-600: hsl(0, 0%, 50%);
  --neutral-900: hsl(0, 0%, 7%);
  --fontWeightBold: 800;
  --fontWeightNormal: 600;
}

@font-face {
  font-family: "Figtree";
  src: url(/assets/fonts/Figtree-VariableFont_wght.ttf) format("truetype");
  font-weight: 600 800;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 1rem;
  line-height: 1.5;
  min-height: 100vh;
  width: 100%;
  background: var(--primary);
  font-family: "Figtree", sans-serif;
}

.card__topic {
  padding: 0.3em 1em;
  font-size: 0.8rem;
  font-weight: var(--fontWeightBold);
  background: var(--primary);
  display: inline-block;
  margin-bottom: 1em;
  border-radius: 0.3em;
}
.card__date {
  font-size: 0.9rem;
  margin-bottom: 0.6em;
  font-weight: var(--fontWeightNormal);
}
.card__title {
  font-size: 1.4rem;
  font-weight: var(--fontWeightBold);
  margin-bottom: 0.9em;
}
.card__title:hover {
  cursor: pointer;
  color: var(--primary);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

.card__illustration {
  display: block;
  margin: auto;
  max-width: 100%;
  width: 100%;
  height: auto;
  border-radius: 0.6em;
  margin: 0 0 1.25em 0;
}

.card {
  width: 23.75em;
  background: var(--neutral-100);
  border-radius: 1.25em;
  padding: 1.3em;
  transition: all 0.3s ease;
  margin: 2em 0;
}
.card:hover {
  box-shadow: 10px 10px 0 0 var(--neutral-900);
  transform: translate(-10px, -10px);
}
.card__description {
  color: var(--neutral-600);
  margin: 0 0 1.25em 0;
  font-weight: var(--fontWeightNormal);
}
.card__autor {
  display: flex;
  justify-content: left;
  align-items: center;
}
.card__autor__image {
  margin-right: 0.6em;
  width: 2.06em;
}
.card__autor__name {
  font-size: 0.8rem;
  font-weight: 800;
  color: var(--neutral-900);
}
.card__autor__name:hover {
  cursor: pointer;
  color: var(--primary);
}

@media only screen and (max-width: 25em) {
  .card {
    max-width: 18.5em;
    padding: 1em;
  }
  .card__title {
    font-size: 1rem;
    margin-bottom: 0.5em;
  }
  .card__date {
    font-size: 0.7rem;
  }
  .card__description {
    font-size: 0.7rem;
  }
}